*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.App{
    background-color: #efefef;
    height: 100vh;
}

.container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sign-option{
    padding: 20px;
    border-radius: 7px;
    flex-grow: 0.3;
    flex-shrink: 10;
    flex-basis: 300px;
}

.sign-option, .sign-option > form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    background-color:rgb(83, 168, 86) ;
}

#register-container > form{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.form-divide{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.btn{
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 10px; 
    width: 280px;
    height: 60px;
}

.submit-btn, input{
    height: 40px;
    border-radius: 7px;
    border: none;
    outline: none;
    padding: 10px;
    background-color: #ffffff8d;
    color: #000;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
}

input[type~="checkbox"]{
    width: 20px;
    height: 20px;
    cursor: pointer;

}

label{
    font-size: 1.2rem;
    font-weight: 600;
    color: #fff;
}
.accountStat{
    cursor: pointer;
    text-decoration: underline;
    color: indigo;
}

@media screen and (max-width: 500px) {
    #register-container > form{
        display: flex;
        flex-direction: column;
    }
}